.subscriptions {
  width: 100%;
  background-color: $color-ghost-white;
}

.subscriptions__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 122px 0 95px 0;
  width: 1160px;
  margin: 0 auto;
  z-index: 1;

  &::after {
    position: absolute;
    content: "";
    width: 321px;
    height: 384px;
    bottom: -11%;
    right: -18%;
    background-image: url("../img/eqiupment@1x.webp");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    z-index: 0;
    transform: rotate(0) scale(1.3);


    @include retina {
      background-image: url("../img/equipment@2x.webp");
    }

    @include vp-1199 {
      width: 289px;
      height: 345px;
      bottom: -4%;
      right: -8%;
      transform: rotate(0) scale(1.3);
    }

    @include vp-767 {
      display: none;
    }
  }

  @include vp-1199 {
    padding: 100px 118px;
    width: 678px;
  }

  @include vp-767 {
    padding: 72px 0 70px 0;
    width: 290px;
  }
}

.subscriptions__wrapper h2 {
  font-size: 54px;
  font-family: $ff-alumni;
  font-style: normal;
  font-weight: 600;
  line-height: 60px;
  letter-spacing: 1.62px;
  word-break: break-all;

  margin: 0;
  padding: 0;

  @include vp-1199 {
    font-size: 42px;
    line-height: 50px;
    letter-spacing: 1.26px;

    padding: 0;
  }

  @include vp-767 {
    font-size: 32px;
    line-height: 24px;
    letter-spacing: 0.96px;

    padding: 0 0 8px 0;
  }
}
