.contacts {
  width: 100%;
  background-color: #ffffff;
}

.contacts__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 1199px) {
  .contacts__wrapper {
    display: flex;
    flex-direction: column;
  }
}

.contacts__free-training {
  color: #ffffff;
  background-color: #102654;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 56px 170px 68px calc(100% - 580px);
}
@media (max-width: 1199px) {
  .contacts__free-training {
    padding: 70px 163px 68px 133px;
  }
}
@media (max-width: 767px) {
  .contacts__free-training {
    padding: 70px 15px 68px 15px;
  }
}

.contacts__free-training h3 {
  font-size: 54px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 800;
  line-height: 156%;
  letter-spacing: 2.4px;
  margin: 0;
}
@media (max-width: 1199px) {
  .contacts__free-training h3 {
    font-size: 42px;
    font-weight: 700;
    line-height: 85%;
    letter-spacing: 1.26px;
    padding: 0 0 20px 0;
  }
}
@media (max-width: 767px) {
  .contacts__free-training h3 {
    font-size: 32px;
    font-weight: 600;
    letter-spacing: 0.96px;
  }
}

.contacts__form {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 27px;
}
@media (max-width: 1199px) {
  .contacts__form {
    column-gap: 60px;
    row-gap: 30px;
  }
}
@media (max-width: 767px) {
  .contacts__form {
    display: flex;
    flex-direction: column;
    row-gap: 17px;
  }
}

.contacts__form-button {
  justify-content: center;
  padding: 18px 50px 16px 50px;
  border: 3px solid #ffffff;
  grid-column: 1/-1;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  margin-top: auto;
}
@media (max-width: 1199px) {
  .contacts__form-button {
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.7px;
    text-transform: uppercase;
    padding: 15px 54px;
  }
}
@media (max-width: 767px) {
  .contacts__form-button {
    margin-top: 21px;
    padding: 15px 49px;
  }
}

.contacts__info {
  background-color: #ffffff;
  color: #102654;
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 58px calc(100% - 580px) 60px 120px;
  border-top: 2px solid #74819c;
}
@media (max-width: 1199px) {
  .contacts__info {
    padding: 60px 133px 63px 133px;
  }
}
@media (max-width: 767px) {
  .contacts__info {
    padding: 62px 15px 63px 15px;
    border: none;
  }
}

.contacts__info h3 {
  font-size: 54px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  word-break: break-all;
  margin: 0;
  padding: 0 0 37px 0;
}
@media (max-width: 1199px) {
  .contacts__info h3 {
    font-size: 42px;
    letter-spacing: 1.26px;
    padding: 0 0 32px 0;
  }
}
@media (max-width: 767px) {
  .contacts__info h3 {
    font-size: 32px;
    letter-spacing: 0.96px;
    padding: 0 0 35px 0;
  }
}

.contacts__info dl {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-flow: dense;
  margin: 0;
}
@media (max-width: 1199px) {
  .contacts__info dl {
    grid-template-columns: 45% 1fr;
  }
}
.contacts__info dl::after {
  position: absolute;
  bottom: 50%;
  left: 183px;
  content: "";
  width: 102px;
  height: 2px;
  background-color: #74819c;
  transform: rotate(90deg);
}
@media (max-width: 1199px) {
  .contacts__info dl::after {
    left: 40%;
    right: 0;
    top: 50%;
    width: 129px;
  }
}
@media (max-width: 767px) {
  .contacts__info dl::after {
    display: none;
  }
}
@media (max-width: 767px) {
  .contacts__info dl {
    display: flex;
    flex-direction: column;
  }
}

.contacts__info dt {
  font-size: 16px;
