.custom-input input {
  color: #ffffff;
  font-size: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  background-color: transparent;
  width: 100%;
  padding: 22px 10px 6px 0;
  border: none;
  border-bottom: 2px solid #74819c;
}
.custom-input input:hover {
  border-bottom: 2px solid #ffffff;
}
.custom-input input:focus {
  border: none;
  outline: 2px solid #ffffff;
}

.custom-input input::placeholder {
  color: #ffffff;
  font-size: 14px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.is-invalid input {
  border-bottom: 2px solid #ff121f;
}
.is-invalid .input-message {
  color: #ff121f;
}

